import React from "react";
import '../App.css';
import { useEffect } from 'react';

import Article1 from '../images/article/1.png';
import Article2 from '../images/article/2.png';
import Article3 from '../images/article/3.png';
import Product1 from '../images/production/1.png';
import Product2 from '../images/production/2.png';
import Product3 from '../images/production/3.png';
import Delivery1 from '../images/delivery/1.svg';
import Delivery2 from '../images/delivery/2.svg';
import Delivery3 from '../images/delivery/3.svg';
import Delivery4 from '../images/delivery/4.svg';

function Production() {

    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    return(
        <>
            <main className="main-production container">
                <div className="container-three">
                    <p className="container-three-header text-header text-align-center">О производстве</p>
                    <div className="production-article d-flex">
                        <div className="article">
                            <img src={Article1} alt="article" />
                            <p>ЗЖБК предлагает широкий выбор типовых железобетонных изделий (ЖБИ). В нашем каталоге представлены все возможные конфигурации стройматериалов, представляющих неизменно высокий интерес для промышленного, жилого и гражданского строительства: фундаментные блоки, плиты перекрытия, перемычки для оконных и дверных проемов, кольца, прогоны для дорог и мостов, ленточные фундаменты, бордюрные камни, дорожные бетонные плиты, опорные подушки. Кроме продаж имеющегося ассортимента товаров, мы также принимаем заказы на изготовление ЖБИ-изделий по образцу заказчика.</p>
                        </div>
                        <div className="article">
                            <img src={Article2} alt="article" />
                            <p>Для организации доставки продукции в ЗЖБК создан логистический центр. Для доставки на строительные объекты в пределах Свердловской области мы используем собственный автопарк — это позволяет предоставлять заказчикам недорогие услуги по перевозке ЖБИ в оптимальные сроки. В то же время в нашей компании внедрена эффективная логистическая схема взаимодействия с надежными партнерами по междугородним автомобильным или железнодорожным грузоперевозкам. Это дает отличную возможность поставлять крупные партии ЖБИ на строительные объекты в соседние и даже относительно удаленные регионы по приемлемым ценам.</p>
                        </div>
                        <div className="article">
                            <img src={Article3} alt="article" />
                            <p>ЗЖБК, кроме продажи товаров, имеющихся на складе в наличии, принимает также заказы на изготовление прочих стандартных и нестандартных железобетонных изделий по индивидуальным чертежам и образцам заказчика. Мы готовы взяться за разработку, изготовление и поставку ЖБИ-изделий любой технологической сложности в договорные сроки и с гарантией неизменно высокого качества нашей продукции. Самое современное оборудование и собственная лаборатория по контролю над качеством, а главное, опытные специалисты и инженеры завода позволяют нам обеспечить такую возможность.</p>
                        </div>
                    </div>
                    <p className="container-three-header text-header text-align-center margin-top-40">Производство</p>
                    <div className="container-three-production">
                        <div className="production1 d-flex">
                            <div className="production1-one">
                                <p>Наш завод ЖБИ основан в 2015 году. В самом начале мы научились продавать и организовывать логистику для потребителей железобетонных изделий. Затем мы пошли дальше. Что бы ни утонуть в современном, быстроменяющемся рынке ЖБИ изделий, где вытесняются посредники государственными структурами и пожеланиями потребителей, коллективом завода принято решение, об организации производства ЖБИ изделий.</p>
                            </div>
                            <div className="production1-two">
                                <img src={Product1} alt="production" />
                            </div>
                        </div>
                        <div className="production2 d-flex">
                            <div className="production2-two">
                                <img src={Product2} alt="production" />
                            </div>
                            <div className="production2-one">
                                <p>Производственные площади разместились в пригороде, где на сегодняшний день и продолжается производственный процесс. Компания четко ориентируется на рынке, ассортимент продукции расширяется, при этом качество продукции только растёт.</p>
                            </div>
                        </div>
                        <div className="production3 d-flex">
                            <div className="production3-one">
                                <p>Все это отражается на ценовой политике и позволяет поддерживать ее на достаточно низком уровне. В 2018 году было принято решение о закупке нового оборудования EDIL STAMPI, KFPK PP, HANGCHA. Что позволило нашему заводу производить железобетонные изделия в больших объемах. Новейшие технологические разработки позволяют выпускать продукцию высочайшего качества, которая полностью соответствует как российским, так и зарубежным стандартам.</p>
                            </div>
                            <div className="production3-two">
                                <img src={Product3} alt="production" />
                            </div>
                        </div>
                    </div>
                    <p className="container-three-header text-header text-align-center margin-top-40">Доставка</p>
                    <div className="production-delivery d-flex">
                        <div className="delivery">
                            <img src={Delivery1} alt="delivery" />
                            <p>Авто транспорт</p>
                        </div>
                        <div className="delivery">
                            <img src={Delivery2} alt="delivery" />
                            <p>Водный транспорт</p>
                        </div>
                        <div className="delivery">
                            <img src={Delivery3} alt="delivery" />
                            <p>Железная дорога</p>
                        </div>
                        <div className="delivery delivery-img4">
                            <img src={Delivery4} alt="delivery" />
                            <p>Комбинированная доставка</p>
                        </div>
                    </div>
                </div>
            </main>
        </>
    );
}

export default Production;