import { createSlice } from '@reduxjs/toolkit';

export const cartSlice = createSlice({
    name : 'cart',
    initialState : {
        value : {}
    },
    reducers : {
        increment : (state, data) => {
            let id = data.payload;
            if(state.value[id] !== undefined) {
                state.value[id]++;
            } else {
                state.value[id] = 1;
            }
        },
        minus : (state, data) => {
            let id = data.payload;
            if(state.value[id] > 1) {
                state.value[id]--;
            } else {
                state.value[id] = 1;
            }
        },
        plus : (state, data) => {
            let id = data.payload;
            state.value[id]++;
        },
        del : (state, data) => {
            delete state.value[data.payload];
        },
        delAll : (state, data) => {
            state.value = {};
        }
    },
});

export const { increment, minus, plus, del, delAll } = cartSlice.actions;
export const selectCart = state => state.cart.value;
export default cartSlice.reducer;