import React, { useState } from "react";
import './App.css';
import { useEffect } from 'react';

import prodsArr from './product.json';
import { useDispatch } from 'react-redux';
import { increment } from './store/cartSlice';

import ImgOne from './images/img-one.png';
import Catalog1 from './images/catalog/1.png';
import Catalog2 from './images/catalog/2.png';
import Catalog3 from './images/catalog/3.png';
import Catalog4 from './images/catalog/4.png';
import Catalog5 from './images/catalog/5.png';
import Catalog6 from './images/catalog/6.png';
import Catalog7 from './images/catalog/7.png';
import Catalog8 from './images/catalog/8.png';
import Allcatalog from './images/all-catalog.svg';
import Product1 from './images/production/1.png';
import Product2 from './images/production/2.png';
import Product3 from './images/production/3.png';
import Advantage1 from './images/advantage/1.svg';
import Advantage2 from './images/advantage/2.svg';
import Advantage3 from './images/advantage/3.svg';
import Advantage4 from './images/advantage/4.svg';
import Advantage5 from './images/advantage/5.svg';
import Advantage6 from './images/advantage/6.svg';
import Add from './images/add.svg';

import {Link} from 'react-router-dom';

function Main() {

    const dispatch = useDispatch();
    
    function addBasket(element) {
        element.preventDefault();
        for(let key of prodsArr) {
            if(element.target.dataset.p === key.id) {
                let arr = [];
                arr.push(key.id, key.title, key.cost);
                dispatch(increment(arr));
            }
        }
        openMessage('Добавлено в корзину');
    }

    function openMessage(message, timeClose = 2000) {
        let popUp = document.createElement('div');
        popUp.classList.add('pop-up-message');
        popUp.innerHTML = message;
        openFieldmessage(popUp);
        setTimeout(function() {
            closeMessage(popUp);
        }, timeClose);
    }
    
    function openFieldmessage(popUp) {
        let fieldMessage = document.querySelector('.field-message');
        if(fieldMessage) {
            fieldMessage.append(popUp);
        }
        else {
            let fieldMessage = document.createElement('div');
            fieldMessage.classList.add('field-message');
            document.querySelector('body').append(fieldMessage);
            fieldMessage.append(popUp);
        }
    }
    
    function closeMessage(popUp) {
        popUp.remove();
        let allPopUp = document.querySelectorAll('.field-message .pop-up-message');
        if(allPopUp.length === 0) {
            document.querySelector('.field-message').remove();
        }
    }

    function delMess(box) {
        box.innerHTML = '';
    }

    const [inp1, setInp1] = useState('');
    const [inp2, setInp2] = useState('');
    const [inp3, setInp3] = useState('');
    const [inp4, setInp4] = useState('privacy-check');

    const [mod, setMod] = useState('box-modal d-none');

    function placeMail() {
        let input1 = document.querySelector('#i1');
        let input2 = document.querySelector('#i2');
        let input3 = document.querySelector('#i3');
        let check = document.querySelector('.privacy-check');
        let text = document.querySelector('.order-text-stop');
        if(input1.value === '' || input2.value === '' || input3.value === '' || !check.checked) {
            text.innerHTML = 'Заполните все поля';
            setTimeout(function() {
                delMess(text);            
            }, 2000); 
        }
        if(input1.value === '') {
            setInp1('red');
            setTimeout(function() {
                setInp1('') ;          
            }, 2000);
        }
        if(input2.value === '') {
            setInp2('red');
            setTimeout(function() {
                setInp2('') ;          
            }, 2000);
        }
        if(input3.value === '') {
            setInp3('red');
            setTimeout(function() {
                setInp3('') ;          
            }, 2000);
        }
        if(!check.checked) {
            setInp4('privacy-check red');
            setTimeout(function() {
                setInp4('privacy-check') ;          
            }, 2000);
        }
        if(input1.value !== '' && input2.value !== '' && input3.value !== '' && check.checked) {
            setMod('box-modal');
        }
    }

    function closeMod() {
        let input1 = document.querySelector('#i1');
        let input2 = document.querySelector('#i2');
        let input3 = document.querySelector('#i3');
        let check = document.querySelector('.privacy-check');
        setMod('box-modal d-none');
        input1.value = '';
        input2.value = '';
        input3.value = '';
        check.checked = false;
    }

    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    return(
        <>
            <main className="container-main container">
                <div className="container-one">
                    <div className="container-one-content d-flex">
                        <div className="one-content-text d-flex justify-content-center">
                            <p className="one-text1 text-header">Производство железобетонных конструкций для промышленных объектов</p>
                            <p className="one-text2">Компания чётко ориентируется на рынке, ассортимент продукции постоянно расширяется, при этом качество продукции только растет. Все это отражается на ценовой политике и позволяет поддерживать ее на низком уровне</p>
                        </div>
                        <div className="one-content-img d-flex align-items-center">
                            <img src={ImgOne} alt="banner" />
                        </div>
                    </div>
                </div>
                <div className="container-two">
                    <p className="container-two-header text-header">Наш каталог</p>
                    <div className="container-two-catalog">
                        <div className="two-catalog-row1 d-flex">
                            <div className="product">
                                <div className="product-img">
                                    <img src={Catalog1} alt="product" />
                                </div>
                                <div className="product-text">
                                    <Link className="out-link" to="/catalog/ring-weighting">
                                        <p className="product-text1">Утяжелитель кольцевой 2 УТК 325</p>
                                    </Link>
                                    <p className="product-text2">Д/Ш/В 1200x600x320 мм<br />Вес 0.28 т | Объем  0.12 м3</p>
                                    <p className="product-text3">2 200 руб</p>
                                </div>
                                <button onClick={addBasket} data-p="1">В корзину</button>
                            </div>
                            <div className="product">
                                <div className="product-img">
                                    <img src={Catalog2} alt="product" />
                                </div>
                                <div className="product-text">
                                    <Link className="out-link" to="/catalog/tray-lc">
                                        <p className="product-text1">Лоток ЛК 300.45.30</p>
                                    </Link>
                                    <p className="product-text2">Д/Ш/В 2990x430x280 мм<br />Вес 0.45 т | Объем  0.18 м3</p>
                                    <p className="product-text3">2 100 руб</p>
                                </div>
                                <button onClick={addBasket} data-p="2">В корзину</button>
                            </div>
                            <div className="product">
                                <div className="product-img">
                                    <img src={Catalog3} alt="product" />
                                </div>
                                <div className="product-text">
                                    <Link className="out-link" to="/catalog/wall-ring-cs">
                                        <p className="product-text1">Кольцо стеновое КС 10.3 / КЦ 10-3</p>
                                    </Link>
                                    <p className="product-text2">Д/Ш/В 1160x1160x290 мм<br />Вес 0.25 т | Объем  0.08 м3</p>
                                    <p className="product-text3">1 200 руб</p>
                                </div>
                                <button onClick={addBasket} data-p="3">В корзину</button>
                            </div>
                            <div className="product">
                                <div className="product-img">
                                    <img src={Catalog4} alt="product" />
                                </div>
                                <div className="product-text">
                                    <Link className="out-link" to="/catalog/foundation-block-slab">
                                        <p className="product-text1">Блок (плита) фундамента Ф 1</p>
                                    </Link>
                                    <p className="product-text2">Д/Ш/В 1500x1250x200 мм<br />Вес 0.95 т | Объем  0.38 м3</p>
                                    <p className="product-text3">3 100 руб</p>
                                </div>
                                <button onClick={addBasket} data-p="4">В корзину</button>
                            </div>
                        </div>
                        <div className="two-catalog-row2 d-flex">
                            <div className="product">
                                <div className="product-img">
                                    <img src={Catalog5} alt="product" />
                                </div>
                                <div className="product-text">
                                    <Link className="out-link" to="/catalog/foundation-block-lekal">
                                        <p className="product-text1">Блок фундамента лекальный Ф1л/п-15.165</p>
                                    </Link>
                                    <p className="product-text2">Д/Ш/В 1650x1100x1000 мм<br />Вес 3.5 т | Объем  1.44 м3</p>
                                    <p className="product-text3">5 400 руб</p>
                                </div>
                                <button onClick={addBasket} data-p="5">В корзину</button>
                            </div>
                            <div className="product">
                                <div className="product-img">
                                    <img src={Catalog6} alt="product" />
                                </div>
                                <div className="product-text">
                                    <Link className="out-link" to="/catalog/pipe-round-link">
                                        <p className="product-text1">Звено трубы круглое ЗК 4.200</p>
                                    </Link>
                                    <p className="product-text2">Д/Ш/В 2000x1240x1240 мм<br />Вес 2.1 т | Объем  0.84 м3</p>
                                    <p className="product-text3">4 700 руб</p>
                                </div>
                                <button onClick={addBasket} data-p="6">В корзину</button>
                            </div>
                            <div className="product">
                                <div className="product-img">
                                    <img src={Catalog7} alt="product" />
                                </div>
                                <div className="product-text">
                                    <Link className="out-link" to="/catalog/bottom-plate">
                                        <p className="product-text1">Плита днища ПН 10 / КЦД 10</p>
                                    </Link>
                                    <p className="product-text2">Д/Ш/В 1200x1200x120 мм<br />Вес 0.44 т | Объем  0.18 м3</p>
                                    <p className="product-text3">2 500 руб</p>
                                </div>
                                <button onClick={addBasket} data-p="7">В корзину</button>
                            </div>
                            <div className="product">
                                <div className="product-img">
                                    <img src={Catalog8} alt="product" />
                                </div>
                                <div className="product-text">
                                    <Link className="out-link" to="/catalog/belt-weighting">
                                        <p className="product-text1">Утяжелитель поясной УБП-0.3</p>
                                    </Link>
                                    <p className="product-text2">Д/Ш/В 1500x250x250 мм<br />Вес 0.23 т | Объем  0.094 м3</p>
                                    <p className="product-text3">1 100 руб</p>
                                </div>
                                <button onClick={addBasket} data-p="8">В корзину</button>
                            </div>
                        </div>
                        <Link className="white-catalog-all" to="/catalog">
                            <div className="two-catalog-all d-flex align-items-center justify-content-center">
                                <img src={Allcatalog} alt="catalog" />
                                <p>Весь каталог</p>
                            </div>
                        </Link>
                    </div>
                </div>
                <div className="container-three">
                    <p className="container-three-header text-header">О производстве</p>
                    <div className="container-three-production">
                        <div className="production1 d-flex">
                            <div className="production1-one">
                                <p>Наш завод ЖБИ основан в 2015 году. В самом начале мы научились продавать и организовывать логистику для потребителей железобетонных изделий. Затем мы пошли дальше. Что бы ни утонуть в современном, быстроменяющемся рынке ЖБИ изделий, где вытесняются посредники государственными структурами и пожеланиями потребителей, коллективом завода принято решение, об организации производства ЖБИ изделий.</p>
                            </div>
                            <div className="production1-two">
                                <img src={Product1} alt="production" />
                            </div>
                        </div>
                        <div className="production2 d-flex">
                            <div className="production2-two">
                                <img src={Product2} alt="production" />
                            </div>
                            <div className="production2-one">
                                <p>Производственные площади разместились в пригороде, где на сегодняшний день и продолжается производственный процесс. Компания четко ориентируется на рынке, ассортимент продукции расширяется, при этом качество продукции только растёт.</p>
                            </div>
                        </div>
                        <div className="production3 d-flex">
                            <div className="production3-one">
                                <p>Все это отражается на ценовой политике и позволяет поддерживать ее на достаточно низком уровне. В 2018 году было принято решение о закупке нового оборудования EDIL STAMPI, KFPK PP, HANGCHA. Что позволило нашему заводу производить железобетонные изделия в больших объемах. Новейшие технологические разработки позволяют выпускать продукцию высочайшего качества, которая полностью соответствует как российским, так и зарубежным стандартам.</p>
                            </div>
                            <div className="production3-two">
                                <img src={Product3} alt="production" />
                            </div>
                        </div>
                    </div>
                </div>
                <div className="container-four">
                    <p className="container-four-header text-header">Наши преимущества</p>
                    <div className="container-four-advantage">
                        <div className="advantage-row1 d-flex">
                            <div className="advantage">
                                <img src={Advantage1} alt="advantage" />
                                <p className="advantage-text1">ЭФФЕКТИВНАЯ<br />ЛОГИСТИКА</p>
                                <p className="advantage-text2">Готовы организовать поставку ЖБИ на ваши строительные объекты автомобильным или железнодорожным транспортом, для перевозок используем свой автотранспорт, что существенно сокращает стоимость доставки продукции.</p>
                            </div>
                            <div className="advantage">
                                <img src={Advantage2} alt="advantage" />
                                <p className="advantage-text1">НАДЁЖНОСТЬ<br />И ОПЫТ</p>
                                <p className="advantage-text2">Наш завод ЖБИ работает на строительном рынке уже более пяти лет. За это время мы честным трудом заработали свое доброе имя и деловую репутацию, которыми дорожит каждый сотрудник компании. За эти годы наш коллектив накопил ценнейшие знания, которые теперь используются на благо ваших интересов.</p>
                            </div>
                            <div className="advantage">
                                <img src={Advantage3} alt="advantage" />
                                <p className="advantage-text1">ГИБКИЕ ФИНАНСОВЫЕ<br />УСЛОВИЯ</p>
                                <p className="advantage-text2">Мы дорожим каждым клиентом, с пониманием относимся к потребностям и возможностям заказчика при определении условий сотрудничества. Менеджеры компании всегда готовы сделать сотрудничество обоюдовыгодным.</p>
                            </div>
                        </div>
                        <div className="advantage-row2 d-flex">
                            <div className="advantage">
                                <img src={Advantage4} alt="advantage" />
                                <p className="advantage-text1">КАЧЕСТВО<br />ПРОДУКЦИИ</p>
                                <p className="advantage-text2">Вся продукция нашего завода ЖБИ строго соответствует требованиям ГОСТ. Мы тщательно отбираем поставщиков сырья для нашего производства. В штате компании работают только опытные специалисты, что обеспечивает профессиональный и внимательный контроль качества продукции на всех этапах производства.</p>
                            </div>
                            <div className="advantage">
                                <img src={Advantage5} alt="advantage" />
                                <p className="advantage-text1">ИНДИВИДУАЛЬНЫЙ<br />ЗАКАЗ</p>
                                <p className="advantage-text2">Наличие современного оборудования, собственный металлоцех, паровые камеры, цех изготовления бетонной смеси и  специалисты. Мы готовы поставить железобетонные изделия любой сложности, возьмемся за изготовление нестандартных изделий по вашим чертежам и проектам.</p>
                            </div>
                            <div className="advantage">
                                <img src={Advantage6} alt="advantage" />
                                <p className="advantage-text1">КОМПЛЕКСНОЕ<br />СНАБЖЕНИЕ</p>
                                <p className="advantage-text2">У нас вы можете совершить закупку материалов и изделий для каждого этапа строительства: от обустройства котлована на площадке, до благоустройства территории.</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="container-five">
                    <div className="container-five-connection">
                        <p className="text-header text-align-center">Связаться с нами</p>
                        <div className="connection">
                            <div className="connection-input d-flex">
                                <input id="i1" className={inp1} placeholder="Имя" type="text" />
                                <input id="i2" className={inp2} placeholder="E-mail" type="text" />
                            </div>
                            <div className="connection-textarea">
                                <textarea id="i3" className={inp3} placeholder="Ваше сообщение"></textarea>
                            </div>
                            <div className="order-text-stop text-align-center"></div>
                            <div className="connection-button d-flex">
                                <button className="but-send" onClick={placeMail}>Отправить</button>
                                <div className="d-flex align-items-center">
                                    <input type="checkbox" className={inp4} />
                                    <p className="privacy">Я согласен
                                        <Link className="privacy-link" to="/privacy-policy">
                                            <span>с политикой конфиденциальности</span>
                                        </Link>
                                    </p>
                                </div>
                            </div>
                            <div className={mod}>
                                <div className="modal">
                                    <img src={Add} alt="OK" />
                                    <p>Сообщение отправлено</p>
                                    <button className="ok" onClick={closeMod}>OK</button>
                                </div>
                            </div>
                        </div>
                    </div> 
                </div>
            </main>
        </>
    );
}

export default Main;