import React from "react";
import '../App.css';
import { useEffect } from 'react';

import Contactcard from '../images/contact-card.png';
import Worker1 from '../images/worker/1.png';
import Worker2 from '../images/worker/2.png';

function Contact() {

    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    return(
        <>
            <main className="main-contact container">
                <div className="container-contact-one">
                    <p className="text-header text-align-center">Контакты</p>  
                    <div className="contact-img-info d-flex">
                        <div className="contact-img">
                            <img src={Contactcard} alt="card" />
                        </div>
                        <div className="contact-info">
                            <div>
                                <p className="contact-info-city">Эдинбург Линейная, 5</p>
                            </div>
                            <div>
                                <p className="contact-info-header">Индекс</p>
                                <p>624002</p>
                            </div>
                            <div>
                                <p className="contact-info-header">Телефоны</p>
                                <p>+375 00 000-00-00</p>
                            </div>
                            <div>
                                <p className="contact-info-header">Email</p>
                                <p>zgbk@zgbk<br />zgbk@yandex.ru<br />zgbk@mail.ru</p>
                            </div>
                        </div>
                    </div>
                    <p className="text-header text-align-center margin-top-40">Наши менеджеры</p> 
                    <div className="contact-worker d-flex justify-content-center">
                        <div className="worker">
                            <img src={Worker1} alt="worker" />
                            <p className="worker-name">Иванова<br />Иванна Ивановна</p>
                            <p className="worker-tel">+375 00 000-00-00</p>
                        </div>
                        <div className="worker">
                            <img src={Worker2} alt="worker" />
                            <p className="worker-name">Петрова<br />Жанна Петровна</p>
                            <p className="worker-tel">+375 00 000-00-00</p>
                        </div>
                    </div>         
                </div>
            </main>
        </>
    );
}

export default Contact;