import React, { useState } from "react";
import '../App.css';
import { useEffect } from 'react';

import {useSelector } from 'react-redux';
import { 
    selectCart 
} from '../store/cartSlice';
import { useDispatch } from 'react-redux';
import { minus } from '../store/cartSlice';
import { plus } from '../store/cartSlice';
import { del } from '../store/cartSlice';
import { delAll } from '../store/cartSlice';
import prodsArr from '../product.json';
import Add from '../images/add.svg';

function Basket() {

    const cart = useSelector(selectCart);
    let arrayCart = [];
    for(let key in cart) {
        arrayCart.push(key.split(','));
    }

    const dispatchBask = useDispatch();
    
    function delBasket(element) {
        element.preventDefault();
        for(let key of prodsArr) {
            if(element.target.dataset.d === key.id) {
                let arr = [];
                arr.push(key.id, key.title, key.cost);
                dispatchBask(del(arr));
            }
        }
    }

    function minusBask(element) {
        element.preventDefault();
        for(let key of prodsArr) {
            if(element.target.dataset.d === key.id) {
                let arr = [];
                arr.push(key.id, key.title, key.cost);
                dispatchBask(minus(arr));
            }
        }
    }

    function plusBask(element) {
        element.preventDefault();
        for(let key of prodsArr) {
            if(element.target.dataset.d === key.id) {
                let arr = [];
                arr.push(key.id, key.title, key.cost);
                dispatchBask(plus(arr));
            }
        }
    }

    function delMess(box) {
        box.innerHTML = '';
    }

    const [inp1, setInp1] = useState('');
    const [inp2, setInp2] = useState('');
    const [inp3, setInp3] = useState('');

    const [mod, setMod] = useState('box-modal d-none');

    function placeOrder() {
        let input1 = document.querySelector('#i1');
        let input2 = document.querySelector('#i2');
        let input3 = document.querySelector('#i3');
        let ul = document.querySelector('.basket-content-info ul');
        let text = document.querySelector('.order-text-stop');
        if(ul.innerHTML === '' || input1.value === '' || input2.value === '' || input3.value === '') {
            text.innerHTML = 'Для оформления заказа должны быть заполнены все обязательные поля и добавлен товар в корзину';
            setTimeout(function() {
                delMess(text);            
            }, 2000); 
        }
        if(input1.value === '') {
            setInp1('red');
            setTimeout(function() {
                setInp1('') ;          
            }, 2000);
        }
        if(input2.value === '') {
            setInp2('red');
            setTimeout(function() {
                setInp2('') ;          
            }, 2000);
        }
        if(input3.value === '') {
            setInp3('red');
            setTimeout(function() {
                setInp3('') ;          
            }, 2000);
        }
        if(ul.innerHTML !== '' && input1.value !== '' && input2.value !== '' && input3.value !== '') {
            setMod('box-modal');
        }
    }

    function closeMod(element) {
        element.preventDefault();
        setMod('box-modal d-none');
        dispatchBask(delAll());
        document.querySelector('#i1').value = '';
        document.querySelector('#i2').value = '';
        document.querySelector('#i3').value = '';
        document.querySelector('.contact-information-textarea textarea').value = '';
    }

    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);
    
    return(
        <>
            <main className="main-basket container">
                <div className="container-basket-one">
                    <p className="text-header text-align-center">Оформление заказа</p>
                    <div className="basket-content">
                        <div className="basket-content-info">
                            <div className="basket-table d-flex">
                                <div className="basket-position">Позиция</div>
                                <div className="basket-amount">Количество</div>
                                <div className="basket-cost">Цена</div>
                                <div className="basket-sum">Сумма</div>
                                <div></div>
                            </div>
                            <ul>
                                {arrayCart.map(item => 
                                <li className="basket-table d-flex align-items-center" key={item[0]}>
                                    <div className="basket-position">{item[1]}</div>
                                    <div className="basket-amount d-flex align-items-center">
                                        <button className="but-minus" onClick={minusBask} data-d={item[0]}>-</button>
                                        <p className="text-align-center">{cart[item]}</p>
                                        <button className="but-plus" onClick={plusBask} data-d={item[0]}>+</button>
                                    </div>
                                    <div className="basket-cost">{item[2]} руб</div>
                                    <div className="basket-sum"><span>{item[2]*cart[item]}</span> руб</div>
                                    <button className="but-close" onClick={delBasket} data-d={item[0]}></button>
                                </li>)}
                            </ul>
                            <div className="contact-information text-align-center">
                                <p className="text-align-center margin-top-40">Контактая информация</p>
                                <div className="contact-information-input-textarea">
                                    <div className="contact-information-input d-flex">
                                        <input id="i1" className={inp1} placeholder="Ваше имя" type="text" />
                                        <input id="i2" className={inp2} placeholder="Телефон" type="tel" />
                                        <input id="i3" className={inp3} placeholder="Email" type="text" />
                                    </div>
                                    <div className="contact-information-textarea">
                                        <textarea placeholder="Коммегарий"></textarea>
                                    </div>
                                </div>
                                <div className="order-text-stop"></div>
                                <button className="but-order" onClick={placeOrder}>Заказать</button>
                            </div>
                            <div className={mod}>
                                <div className="modal">
                                    <img src={Add} alt="OK" />
                                    <p>Заказ принят</p>
                                    <button className="ok" onClick={closeMod}>OK</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </main>
        </>
    );
}

export default Basket;