import React, { useState } from 'react';
import './App.css';

import Logo from './images/logo.svg';
import Allcatalogheader from './images/all-catalog-header.svg';
import Shop from './images/shop.svg';
import Search from './images/search.svg';
import Menuimg from './images/menu.svg';
import closeX from './images/xcl.svg';

import Main from './Main';
import Privacy from './Privacy';
import Catalog from './Catalog/Catalog';
import Out1 from './Catalog/Out1';
import Out2 from './Catalog/Out2';
import Out3 from './Catalog/Out3';
import Out4 from './Catalog/Out4';
import Out5 from './Catalog/Out5';
import Out6 from './Catalog/Out6';
import Out7 from './Catalog/Out7';
import Out8 from './Catalog/Out8';
import Out9 from './Catalog/Out9';
import Out10 from './Catalog/Out10';
import Out11 from './Catalog/Out11';
import Out12 from './Catalog/Out12';
import Out13 from './Catalog/Out13';
import Out14 from './Catalog/Out14';
import Out15 from './Catalog/Out15';
import Out16 from './Catalog/Out16';
import Production from './Production/Production';
import Purchases from './Purchases/Purchases';
import Contact from './Contact/Contact';
import Basket from './Basket/Basket';

import {useSelector } from 'react-redux';
import { 
    selectCart 
} from './store/cartSlice';
import {BrowserRouter, Routes, Route, Link} from 'react-router-dom';

const prodsList = [
    {link : '/catalog/ring-weighting', title : 'Утяжелитель кольцевой 2 УТК 325'},
    {link : '/catalog/tray-lc', title : 'Лоток ЛК 300.45.30'},
    {link : '/catalog/wall-ring-cs', title : 'Кольцо стеновое КС 10.3 / КЦ 10-3'},
    {link : '/catalog/foundation-block-slab', title : 'Блок (плита) фундамента Ф 1'},
    {link : '/catalog/foundation-block-lekal', title : 'Блок фундамента лекальный Ф1л/п-15.165'},
    {link : '/catalog/pipe-round-link', title : 'Звено трубы круглое ЗК 4.200'},
    {link : '/catalog/bottom-plate', title : 'Плита днища ПН 10 / КЦД 10'},
    {link : '/catalog/belt-weighting', title : 'Утяжелитель поясной УБП-0.3'},
    {link : '/catalog/transitional-plate', title : 'Плита переходная П600.98.30'},
    {link : '/catalog/road-plate', title : 'Плита дорожная 2П 18.18-10'},
    {link : '/catalog/foundation', title : 'Фундамент Ф-1'},
    {link : '/catalog/base-plate', title : 'Опорная плита ОП-1к'},
    {link : '/catalog/weighting-1ubkm', title : 'Утяжелитель 1УБКм-426-9'},
    {link : '/catalog/floor-slab', title : 'Плита перекрытия КЦП 2-10'},
    {link : '/catalog/weighting-ubom', title : 'Утяжелитель УБОм-1220'},
    {link : '/catalog/pipe-round-link-3k', title : 'Звено трубы круглое ЗК'}
];

function Header() {

    const cart = useSelector(selectCart);
    let sum = 0;
    for(let key in cart) {
        sum += cart[key];
    }

    const [inpText, setInptext] = useState('');

    const filteredProd = prodsList.filter(item => {
        return (item.title.toLowerCase().includes(inpText.toLowerCase()));
    });

    function startSearch(element) {
        element.preventDefault();
        setInptext(element.target.value);
    }

    function openSearch(element) {
        element.preventDefault();
        document.querySelector('.box-search').classList.toggle('d-none');
    }

    function closeSearch(element) {
        element.preventDefault();
        element.stopPropagation();
        document.querySelector('.box-search').classList.toggle('d-none');
    }

    function closeBoxsearch (element) {
        element.preventDefault();
        document.querySelector('.box-search').classList.toggle('d-none');
    }

    function stopProp(element) {
        element.stopPropagation();
    }

    function openBurger(element) {
        element.preventDefault();
        document.querySelector('.box-menu-burger').classList.remove('d-none');
        document.querySelector('.menu img').setAttribute('src', `${closeX}`);
    }

    function closeBurger(element) {
        element.preventDefault();
        document.querySelector('.box-menu-burger').classList.add('d-none');
        document.querySelector('.menu img').setAttribute('src', `${Menuimg}`);
    }

    function closeAll(element) {
        document.querySelector('.box-menu-burger').classList.add('d-none');
        document.querySelector('.box-search').classList.add('d-none');
        document.querySelector('.menu img').setAttribute('src', `${Menuimg}`);
        document.querySelector('.input-search').value = '';
    }

    return(
        <>
            <BrowserRouter>
            <header className="header container media-menu">
                <div className="container-header">
                    <div className="container-header-one d-flex align-items-center">
                        <div className="logo d-flex align-items-center">
                            <div className="logo-text1 d-flex align-items-center">
                                <img src={Logo} alt="logo" />
                                <p><Link className="white" to="/">ЗЖБК</Link></p>
                            </div>
                            <div className="logo-text2">
                                <p>Производство железобетонных конструкций</p>
                            </div>
                        </div>
                        <div className="container-list">
                            <ul className="list d-flex">
                                <li><Link className="white" to="/catalog"><img src={Allcatalogheader} alt="catalog" />Каталог</Link></li>
                                <li><Link className="white" to="/production">О производстве</Link></li>
                                <li><Link className="white" to="/purchases">Закупки</Link></li>                   
                                <li><Link className="white" to="/contact">Контакты</Link></li>
                                <li className="list-basket"><Link className="white-bask" to="/basket"><img src={Shop} alt="shop" /><p>Корзина</p><span>{sum}</span></Link></li>
                                <li className="search" onClick={openSearch}><img src={Search} alt="search" /><p>Поиск</p></li>
                            </ul>
                        </div>
                        <div className="menu-burger align-items-center">
                            <div>
                                <ul>
                                    <li className="search d-flex align-items-center" onClick={openSearch}><img src={Search} alt="search" />Поиск</li>
                                </ul>
                            </div>
                            <div className="menu"><img src={Menuimg} alt="МЕНЮ" onClick={openBurger} /></div>
                            <div className="box-menu-burger d-none" onClick={closeBurger}>
                                <div className="menu-burger-absolute" onClick={stopProp}>
                                    <ul className="burger-list d-flex">
                                        <li><Link className="menu-orange" to="/catalog" onClick={closeAll}><img className="m-right-5" src={Allcatalogheader} alt="catalog" />Каталог</Link></li>
                                        <li><Link className="menu-orange" to="/production" onClick={closeAll}>О производстве</Link></li>
                                        <li><Link className="menu-orange" to="/purchases" onClick={closeAll}>Закупки</Link></li>                   
                                        <li><Link className="menu-orange" to="/contact" onClick={closeAll}>Контакты</Link></li>
                                        <li className="list-basket"><Link className="white-bask" to="/basket" onClick={closeAll}><img src={Shop} alt="shop" />Корзина<span>{sum}</span></Link></li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="box-search d-none" onClick={closeBoxsearch}>
                        <div className="box-search-absolute" onClick={stopProp}>
                            <div className="box-search-input-but d-flex">
                                <input className="input-search" placeholder="🔍︎ ..." onChange={startSearch} type="text" />
                                <button className="but-search-close" onClick={closeSearch}></button>
                            </div>
                            <ul className="list-search">
                                {filteredProd.map(item => <li key={item.title}><Link className="search-orange" to={item.link} onClick={closeAll}>{item.title}</Link></li>)}
                            </ul>
                        </div>
                    </div>
                </div>
            </header>
            <Routes>
                <Route path='/' element={<Main />}/>
                <Route path='/privacy-policy' element={<Privacy />}/>
                <Route path='/catalog' element={<Catalog />}/>
                <Route path='/catalog/ring-weighting' element={<Out1 />}/>
                <Route path='/catalog/tray-lc' element={<Out2 />}/>
                <Route path='/catalog/wall-ring-cs' element={<Out3 />}/>
                <Route path='/catalog/foundation-block-slab' element={<Out4 />}/>
                <Route path='/catalog/foundation-block-lekal' element={<Out5 />}/>
                <Route path='/catalog/pipe-round-link' element={<Out6 />}/>
                <Route path='/catalog/bottom-plate' element={<Out7 />}/>
                <Route path='/catalog/belt-weighting' element={<Out8 />}/>
                <Route path='/catalog/transitional-plate' element={<Out9 />}/>
                <Route path='/catalog/road-plate' element={<Out10 />}/>
                <Route path='/catalog/foundation' element={<Out11 />}/>
                <Route path='/catalog/base-plate' element={<Out12 />}/>
                <Route path='/catalog/weighting-1ubkm' element={<Out13 />}/>
                <Route path='/catalog/floor-slab' element={<Out14 />}/>
                <Route path='/catalog/weighting-ubom' element={<Out15 />}/>
                <Route path='/catalog/pipe-round-link-3k' element={<Out16 />}/>
                <Route path='/production' element={<Production />}/>
                <Route path='/purchases' element={<Purchases />}/>
                <Route path='/contact' element={<Contact />}/>
                <Route path='/basket' element={<Basket />}/>
            </Routes>
            </BrowserRouter>
        </>
    );
}

export default Header;